// Base theme imports
@import "variables.scss";

// Custom theme overrides
header.masthead {
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.banner-title {
  h1 {
    font-size: 30px;
    font-weight: 700;
    margin-top: 0;
    margin-bottom: 0;
    text-transform: uppercase;
  }
  p {
    font-size: 16px;
    font-weight: 300;
    margin-bottom: 50px;
    color: fade-out(white, .3);
  }
}

@media (min-width: 768px) {
  .banner-title {
    h1 {
      font-size: 50px;
    }
    p {
      font-size: 18px;
      max-width: 80%;
      margin-right: auto;
      margin-left: auto;
    }
  }
}

.banner-click {
  position: absolute;
  bottom: 10%;
  left: 50%;
  transform: translateX(-50%);
}
